.theme-switcher__switch {
  width: 64px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.theme-switcher__switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.theme-switcher__slider {
  cursor: pointer;
  background-color: var(--dark-bg-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.theme-switcher__slider:before {
  content: "";
  height: 30px;
  width: 30px;
  filter: invert();
  background: #fff url("night.a103c86d.svg") center / 80% no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px #2020203d;
}

input:checked + .theme-switcher__slider {
  background-color: var(--bg-color);
}

input:focus + .theme-switcher__slider {
  box-shadow: 0 0 1px var(--font-color);
}

input:checked + .theme-switcher__slider:before {
  filter: invert(0);
  background: #fff url("day.ebffe707.svg") center / 80% no-repeat;
  transform: translateX(34px);
}

.theme-switcher__slider.theme-switcher__slider--round {
  border-radius: 34px;
}

.theme-switcher__slider.theme-switcher__slider--round:before {
  border-radius: 50%;
}

